@font-face {
    font-family: 'EliceDigitalBaeum_Bold';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/EliceDigitalBaeum_Bold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.KiwittEditForm{
    display: flex;
    margin-top: 20px;
    height: 100%;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    padding: 10px;
    transition: 0.3s;
}
.KiwittEditForm:active{
    transform: scale(0.95);
}

.EditBt{
    display: flex;
    flex-direction: column;
}

.Cancel_b{
    font-family: 'Dongle-Regular';
    margin-top: 0px;
    margin-left: 10px;
    width: 90px;
    height: 35px;
    border-radius: 10px;
    border: 0;
    font-size: 25px;
    transition: 0.5s;
    background-color: tomato;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    color: white;
}

.Update_b{
    font-family: 'Dongle-Regular';
    margin-top: 0px;
    margin-left: 10px;
    margin-bottom: 5px;
    width: 90px;
    height: 50px;
    border-radius: 10px;
    border: 0;
    font-size: 35px;
    transition: 0.5s;
    background-color: rgb(56, 194, 118);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    color: white;
}

.KiwittsContainer{
    position: absolute;
    left: 50%;
    width: 100%;
    max-width: 850px;
    transform: translate(-50%, 0);
}

.KiwittForm{
    margin-top: 20px;
    height: 100%;
    min-height: 110px;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    padding: 10px;
    transition: 0.3s;
}
.KiwittForm:active{
    transform: scale(0.95);
}


.Texts{
    display: flex;
    margin-bottom: 5px;
}

.KiwitMessage{
    font-family: 'EliceDigitalBaeum_Bold';
    padding: 10px;
    width: 709px;
    height: 70px;
    border: 0;
    /* border: 1px solid black; */
    border-radius: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    font-size: 40px;
    font-weight: bolder;
}

.KiwitMessage:focus{
    outline: 0;
}

.Kiwitt_b{
    font-family: 'Dongle-Regular';
    margin-top: 0px;
    margin-left: 10px;
    width: 90px;
    height: 90px;
    border-radius: 10px;
    border: 0;
    font-size: 40px;
    transition: 0.5s;
    background-color: rgb(56, 194, 118);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    color: white;
}

.Kiwitt_b:focus{
    outline: 0;
}

.ImgContainer{
    display: flex;
}

.AddedImage{
    width: 20%;
    height: 20%;
    border-radius: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    transition: 0.5s;
}

.AddedImage:hover{
    width: 40%;
    height: 40%;
}

.AddedImage:active{
    width: 100%;
    height: 100%;
}

.AddedImageBtt_Cancel{
    position: absolute;
    margin: 5px;
    width: 30px;
    height: 30px;
    padding-top: 3px;
    border-radius: 15px;
    border: 0;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    background-color: tomato;
    color: white;
    font-weight: bolder;
    font-size: 16px;
    transition: 0.5s;
}   

.AddedImageBtt_Cancel:hover{
    width: 60px;
}

.AddImage{
    color: #04aaff;
    cursor: pointer;
    font-weight: bolder;
    margin-left: 5px;
    /* text-decoration: underline */
}


textarea{
    resize: none;
}

#attach_file{
    display: none;
    /* opacity: 0 */
}