.ProfileContainer{
    position: absolute;
    left: 50%;
    width: 100%;
    max-width: 850px;
    transform: translate(-50%, 0);
}

.Profile{
    display: flex;
    margin-top: 20px;
    height: 100%;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    padding: 20px;
    transition: 0.3s;
    color: rgb(97, 97, 97);
}

.ProfileName{
    font-size: 40px;
    font-weight: bolder;
}

.EditProfile{
    width: 30px;
    height: 30px;
    background-color: unset;
    border: 0;
    cursor: pointer;
    color: rgb(97, 97, 97);;
}

.MyKiwitt{
    display: flex;
    margin-top: 20px;
    height: 100%;
    width: 180px;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    padding: 20px;
    transition: 0.3s;
    color: rgb(97, 97, 97);
}
.MyKiwitt_t{
    font-size: 30px;
    font-weight: bolder;
}

.ProfileForm{
    display: flex;
    margin-top: 20px;
    height: 100%;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    padding: 10px;
    transition: 0.3s;
}

.ProfileFormNewname{
    font-family: 'EliceDigitalBaeum_Bold';
    padding: 10px;
    width: 709px;
    height: 70px;
    border: 0;
    /* border: 1px solid black; */
    border-radius: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    font-size: 40px;
    font-weight: bolder;
}
.ProfileFormNewname:focus{
    outline: 0;
}

.ProfileFormCancel{
    font-family: 'Dongle-Regular';
    margin-top: 0px;
    margin-left: 10px;
    width: 90px;
    height: 35px;
    border-radius: 10px;
    border: 0;
    font-size: 25px;
    transition: 0.2s;
    background-color: tomato;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    color: white;
}
.ProfileFormCancel:active{
    transform: scale(0.9);
}

.ProfileFormUpdate{
    font-family: 'Dongle-Regular';
    margin-top: 0px;
    margin-left: 10px;
    margin-bottom: 5px;
    width: 90px;
    height: 50px;
    border-radius: 10px;
    border: 0;
    font-size: 35px;
    transition: 0.2s;
    background-color: rgb(56, 194, 118);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    color: white;
}
.ProfileFormUpdate:active{
    transform: scale(0.9);
}

.LogoutBt{
    margin-top: 20px;
    width: 150px;
    height: 50px;
    border-radius: 20px;
    border: 0;
    background-color: tomato;
    color: white;
    font-size: 30px;
    font-weight: bolder;
}