body{
    margin: 0;
}
input{
    padding: 0;
}

.init{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 50px;
    color: rgb(37, 37, 37);
    transition: 0.5s;
    text-align: center;
    margin-bottom: 0px;
    font-weight: bolder;
}

.title{
    font-size: 50px;
    color: white;
    transition: 0.5s;
    text-align: center;
    margin-bottom: 0px;
    font-weight: bolder;
}
.subtitle{
    font-size: 20px;
    color: white;
    transition: 0.5s;
    text-align: center;
    margin-bottom: 15px;
}

.Authbackground{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    align-items: center;
    background-color: rgb(37, 37, 37);
}

.container{
    width: 100%;
    max-width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.AuthError{
    margin: 10px 0;
    color: tomato;
    font-size: 15px;
    text-align: center;
}

.AuthSwitch{
    color: #04aaff;
    cursor: pointer;
    margin-top: 0px;
    margin-bottom: 15px;
    display: block;
    font-size: 12px;
    text-decoration: underline;
    transform: translate(0, -2px);
}

.AuthInput{
    max-width: 280px;
    width: 100%;
    height: 45px;
    padding-left: 20px;
    border-radius: 30px;
    box-sizing: content-box;
    margin-bottom: 10px;
    border: 0px;
}

.AuthSubmit{
    max-width: 300px;
    width: 100%;
    height: 40px;
    /*padding: 10px;*/
    border-radius: 30px;
    box-sizing: content-box;
    border: 0px;
    background-color: rgb(77, 113, 231);
    color: white;
    transition: 0.5s;
}
.AuthSubmit:hover{
    transform: scale(1.1);
}
.AuthSubmit:active{
    transform: scale(0.8);
}

.SocialLoginContainer{
    display:flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
}

.SocialLoginGithub{
    width: 50px;
    height: 50px;
    border-radius: 25vh;
    border: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: rgb(37, 37, 37);
    margin: 0px 7px;
    flex-shrink:0;
    transition: 0.5s;
    background-color: white;
}
.SocialLoginGithub:hover{
    width: 140px;
}
.SocialLoginGithub span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.4s;
}
.SocialLoginGithub span:after {
    content: "Login With  Github";
    color: rgb(37, 37, 37);
    font-size: 15px;
    font-weight: bolder;
    position: absolute;
    opacity: 0;
    left: 50px;
    top: 0px;
    transition: 0.5s;
}
.SocialLoginGithub:hover span:after {
    opacity: 1;
    right: 0;
}
.SocialLoginGithub:active{
    transform: scale(0.8);
}

