.KiwittInfo{
    display: flex;
    flex-direction: row;
    padding-bottom: 15px;
    border-bottom: 1px solid rgb(228, 228, 228);
}

.KiwittContainer{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    background-color: white;
    border-radius: 20px;
    padding: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    min-height: 55px;
    transition: 0.5s;
}

.KiwittContainer:hover{
    transform: scale(1.05);
}

.KiwittText{
    font-size: 30px;
    font-weight: 700;
}

.KiwittImg{
    margin-right: 10px;
    width: 30%;
    height: 30%;
    border-radius: 20px;
    transition: 0.5s;
}
.KiwittImg:hover{
    width: 50%;
    height: 50%;
}
.KiwittImg:active{
    width: 100%;
    height: 100%;
}

.KiwittBt{
    position: absolute;
    display: flex;
    flex-direction: column;
    right: -20px;
    transform: translate(0, -10px);
}

.KiwittBt_Delete{
    width: 35px;
    height: 35px;
    border-radius: 15px;
    border: 0;
    background-color: tomato;
    color: white;
    padding: 7.5px;
    margin-bottom: 5px;
    transition: 0.5s;
}

.KiwittBt_Delete:hover{
    transform: scale(1.2);
}
.KiwittBt_Delete:active{
    transform: scale(0.9);
}


.KiwittBt_Edit{
    width: 35px;
    height: 35px;
    border-radius: 15px;
    border: 0;
    background-color: rgb(77, 113, 231);
    color: white;
    padding: 7.5px;
    transition: 0.5s;
}

.KiwittBt_Edit:hover{
    transform: scale(1.2);
}
.KiwittBt_Edit:active{
    transform: scale(0.9);
}

.KiwitterName{
    margin-top: 10px;
    font-weight: 700;
    color: rgb(138, 138, 138);
}

.KiwittBtIcon{
    width: 15px;
    height: 15px;
}