body{
    background-color: rgb(241, 241, 241);
}
a {
    color: inherit; /* blue colors for links too */
    text-decoration: inherit; /* no underline */
}

@font-face {
    font-family: 'Dongle-Regular';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108_2@1.0/Dongle-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.NavigationBg{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 80px;
    max-height: 80px;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    flex-wrap: wrap;
}

.Kiwitter{
    font-family: 'Dongle-Regular';
    margin: 4px 15px;
    font-size: 55px;
    font-weight: bolder;
    flex-shrink:0;
    color: rgb(97, 97, 97);
}

.Home_n{
    font-family: 'Dongle-Regular';
    margin: 11px 15px;
    font-size: 45px;
    font-weight: 500;
    flex-shrink:0;
    color: rgb(97, 97, 97);
}

.Profile_n{
    font-family: 'Dongle-Regular';
